/* ShoeDetail.css */

/* 신발 상세 페이지 컨테이너 스타일 */
.shoe-detail-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* 화면 전체 높이로 설정 */
  padding: 20px; /* 페이지 여백 추가 */
  background-color: #f8f9fa; /* 페이지 배경 색상 */
}

/* 신발 상세 정보 레이아웃 */
.shoe-detail {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff; /* 흰색 배경 */
  padding: 20px;
  border-radius: 8px; /* 둥근 모서리 */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 부드러운 그림자 */
}

/* 신발 이미지 스타일 */
.shoe-image {
  max-width: 40%; /* 이미지 너비를 화면의 80%로 설정 */
  height: auto; /* 이미지 비율 유지 */
  border-radius: 8px; /* 둥근 모서리 */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 부드러운 그림자 */
  margin-bottom: 20px; /* 이미지와 정보 영역 사이의 여백 */
}

/* 신발 정보 영역 */
.shoe-info {
  text-align: center;
  margin-top: 20px;
}

/* 신발 제목 스타일 */
.shoe-title {
  margin-top: 20px;
  font-size: 24px; /* 제목 글자 크기 */
  font-weight: bold; /* 제목 글자 두껍게 */
}

/* 신발 설명 및 가격 스타일 */
.shoe-content,
.shoe-price {
  margin: 10px 0;
  font-size: 18px; /* 설명 및 가격 글자 크기 */
  color: #333333; /* 어두운 회색 텍스트 색상 */
}

/* 주문하기 버튼 스타일 - 빨간색으로 복원 */
.order-button {
  background-color: #dc3545; /* 빨간색 배경 */
  border: none; /* 테두리 없음 */
  color: #ffffff; /* 흰색 글자 */
  padding: 10px 20px; /* 버튼 패딩 */
  font-size: 16px; /* 버튼 글자 크기 */
  cursor: pointer; /* 마우스 포인터 */
  border-radius: 5px; /* 둥근 모서리 */
  margin-top: 20px; /* 버튼 상단 여백 */
}

/* 주문하기 버튼 호버 상태 스타일 */
.order-button:hover {
  background-color: #c82333; /* 호버 시 어두운 빨간색 배경 */
}

/* 탭 내용 스타일 */
.tab-content {
  margin-top: 20px;
  padding: 20px;
  background-color: #ffffff; /* 흰색 배경 */
  border-radius: 8px; /* 둥근 모서리 */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 부드러운 그림자 */
}

/* 탭 제목 스타일 */
.tab-content h5 {
  font-size: 32px; /* 제목 글자 크기 */
  font-weight: bold; /* 제목 글자 두껍게 */
  color: #333333; /* 어두운 회색 텍스트 색상 */
  margin-bottom: 15px; /* 제목 하단 여백 */
}

/* 탭 내용 텍스트 스타일 */
.tab-content p {
  font-size: 16px; /* 텍스트 글자 크기 */
  color: #555555; /* 중간 회색 텍스트 색상 */
  line-height: 1.6; /* 줄 간격 */
  margin-bottom: 10px; /* 텍스트 하단 여백 */
}

/* 탭 내용 리스트 스타일 */
.tab-content ul {
  margin-left: 20px; /* 리스트 왼쪽 여백 */
  list-style-type: disc; /* 불릿 스타일 */
  padding-left: 20px; /* 왼쪽 패딩 추가 */
}

.tab-content ul li {
  font-size: 16px; /* 리스트 아이템 글자 크기 */
  color: #555555; /* 중간 회색 텍스트 색상 */
  line-height: 1.6; /* 줄 간격 */
}

/* 탭 내용 번호 리스트 스타일 */
.tab-content ol {
  margin-left: 20px; /* 리스트 왼쪽 여백 */
  list-style-type: decimal; /* 숫자 리스트 스타일 */
  padding-left: 20px; /* 왼쪽 패딩 추가 */
}

.tab-content ol li {
  font-size: 16px; /* 리스트 아이템 글자 크기 */
  color: #555555; /* 중간 회색 텍스트 색상 */
  line-height: 1.6; /* 줄 간격 */
  margin-bottom: 5px; /* 리스트 아이템 하단 여백 */
}

/* 소제목 스타일 */
.tab-content .sub-heading {
  font-size: 18px; /* 소제목 글자 크기 */
  font-weight: bold; /* 소제목 글자 두껍게 */
  color: #333333; /* 어두운 회색 텍스트 색상 */
  margin-top: 15px; /* 소제목 상단 여백 */
  margin-bottom: 10px; /* 소제목 하단 여백 */
}